.gamePage .selectedPlayer {
  color: #e84393;
}

.gamePage {
  padding: 0px 20px;
  text-align: center;
}

.gamePage button {
  background-color: #ff4e50;
  border: 0px;
  padding: 20px 20px;
  width: 50%;
  font-size: 20px;
  text-transform: capitalize;
  color: #ffffff;
  font-weight: bold;
  display: block;
  margin: 50px auto;
}

.gamePage .game-over {
  color: #ff4e50;
}

.gamePage .game-on {
  color: #bada5f;
}
